.about-wrap {
	.img-wrap {
		width: 300px;
	}
	.flex {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.info-text {
		padding-left: 20px;
		.text-content {
			margin-bottom: 80px;
			max-width: 350px;
			.title {
				font-family: $heading-font;
				font-size: 20px;
				font-weight: 600;
				color: $text-color;
				margin-bottom: 25px;
			}
			.description {
				font-family: $content-font;
				color: $text-color2;
				font-size: 14px;
				margin-bottom: 0;
				font-weight: 500;
				letter-spacing: 1px;
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}
